import React, { Component } from 'react';
import styled, { ThemeProvider } from 'styled-components';
import Grid from 'styled-components-grid';
import block1 from '../static/block1.png';
import block2 from '../static/block2.png';
import block3 from '../static/block3.png';
import block4 from '../static/block4.png';
import block5 from '../static/block5.png';
import block6 from '../static/block6.png';

const Banner = styled.div`
    width: 100%;
    background-color: #fff;
`
const Wrapper = styled.div`
    width: 85%;
    margin: 0px auto;
`
const theme = {
    breakpoints: {
        xs: 0,
        sm: 576,
        md: 768,
        lg: 992,
        xl: 1200
    }
}
const Card = styled.div`
    padding: 6%;
`
const Header = styled.div`
    text-align: center;
`
const Context = styled.div`
    padding: 3%;
    color: #999;
    line-height: 1.8em;
`
const Title = styled.div`
    color: #555;
    padding: 3%;
    font-weight: bold;
`
const Img = styled.img`
    
`

export default class Center extends Component {
    render (){
        return (
            <Banner>
                <Wrapper>
                    <ThemeProvider theme={theme}>
                        <Grid>
                            <Grid.Unit size={ {xs: 1/1, sm: 1/2, md: 1/3} }>
                                <Card>
                                    
                                    <Header>
                                        <Img src={block1}/>
                                        <Title></Title>
                                    </Header>
                                    <Context>
                                        epihero
                                    </Context>

                                </Card>
                            </Grid.Unit>
                            <Grid.Unit size={ {xs: 1/1, sm: 1/2, md: 1/3} }>
                                <Card>
                                    <Header>
                                        <Img src={block2}/>
                                        <Title></Title>
                                    </Header>
                                    <Context>
                                        epihero90%epihero
                                    </Context>
                                </Card>
                            </Grid.Unit>
                            <Grid.Unit size={ {xs: 1/1, sm: 1/2, md: 1/3} }>
                                <Card>

                                    <Header>
                                        <Img src={block3}/>
                                        <Title></Title>
                                    </Header>
                                    <Context>
                                        epihero
                                    </Context>
                                </Card>
                            </Grid.Unit>
                            <Grid.Unit size={ {xs: 1/1, sm: 1/2, md: 1/3} }>
                                <Card>

                                    <Header>
                                        <Img src={block4}/>
                                        <Title></Title>
                                    </Header>
                                    <Context>
                                        epiheroepihero
                                    </Context>
                                </Card>
                            </Grid.Unit>
                            <Grid.Unit size={ {xs: 1/1, sm: 1/2, md: 1/3} }>
                                <Card>

                                    <Header>
                                        <Img src={block5}/>
                                        <Title></Title>
                                    </Header>
                                    <Context>
                                        epihero
                                    </Context>
                                </Card>
                            </Grid.Unit>
                            <Grid.Unit size={ {xs: 1/1, sm: 1/2, md: 1/3} }>
                                <Card>

                                    <Header>
                                        <Img src={block6}/>
                                        <Title></Title>
                                    </Header>
                                    <Context>
                                        epihero
                                    </Context>
                                </Card>
                            </Grid.Unit>
                        </Grid>
                    </ThemeProvider>
                </Wrapper>
            </Banner>
        )
    }
}



